@import "./variables";

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

body {
  margin: 0px;
  height: 100%;
  font-family: "Poppins", sans-serif;
  background-color: $primaryLight;
  color: white;
  max-width: 100%;
}

#dark {
  transition: background-color 0.5s ease;
  background-color: black;
}

.App {
  display: grid;
  width: 100%;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.hb-btn {
  position: fixed;
  border-radius: 50%;
  height: 5vh;
  width: 5vh;
  top: 20px;
  right: 20px;
  background-color: $tertiaryLight;
  border: none;
  display: none;
  cursor: pointer;
  z-index: 3;
}

#dark .hb-btn {
  transition: background-color 0.5s ease;
  background-color: $tertiaryDark;
}

.hb-menu {
  position: fixed;
  background-color: $tertiaryLight;
  height: 100%;
  width: 100%;
  z-index: 2;
}

#dark .hb-menu {
  transition: background-color 0.5s ease;
  background-color: $primaryDark;
}

.hamburger__wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  padding-top: 20vh;
  img {
    height: 60px;
    width: auto;
  }
}

.hb-links {
  display: flex;
  flex-direction: column;
  a {
    font-size: 3rem;
    text-align: center;
  }
}

.menu-icon {
  fill: white;
}

.navbar {
  max-width: 100%;
  position: sticky;
  top: 0;
  left: 0;
  min-height: 6%;
  padding: 10px 28vw;
  z-index: 1;
  background-color: $primaryLight;
}

#dark .navbar {
  transition: background-color 0.5s ease;
  background-color: $primaryDark;
}

.navbar__wrapper {
  max-width: 45vw;
  display: flex;
  justify-content: space-between;
  margin: auto;
  font-size: 1.5rem;
}

.nav-left {
  display: flex;
  justify-content: left;
  gap: 20px;
}

#hb-switch {
  position: absolute;
  top: 5vw;
  left: 5vw;
}

.switch {
  position: relative;
  width: 60px;
  height: 30px;
}

.switch input {
  width: 0;
  height: 0;
  opacity: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  border-radius: 34px;
}

#dark .slider {
  transition: background-color 0.5s ease;
  background-color: $primaryLight;
}

.switch-left {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  content: "";
  height: 26px;
  width: 28px;
  left: 3px;
  bottom: 2.5px;
  background-color: white;
  border-radius: 50%;
  background-color: $primaryLight;
}

.sun-icon {
  height: 80%;
  width: auto;
}

#dark .switch-left {
  display: none;
}

.switch-right {
  position: absolute;
  content: "";
  height: 26px;
  width: 28px;
  left: 29px;
  bottom: 2.5px;
  background-color: white;
  border-radius: 50%;
  display: none;
  justify-content: center;
  align-items: center;
}

#dark .switch-right {
  display: flex;
  transition: background-color 0.5s ease;
  background-color: $kentoYellow;
}


a {
  color: white;
  text-decoration: none;
}

// HERO

.hero {
  width: 100%;
  min-height: 75vh;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10rem;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.hero__wrapper {
  font-size: min(4vw, 6rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  animation: fadeIn 3s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.dev-title,
.web-title,
.first-name,
.last-name {
  margin: 0;
  padding: 0;
  display: inline-block;
}

.first-name,
.dev-title {
  color: $tertiaryLight;
}

#dark .first-name, .dev-title {
  color: $secondaryLight;
  transition: background-color 0.5s ease;
}

// About


.about {
  background-color: $secondaryLight;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0;
  padding-bottom: 5vw;
}

#dark .about {
  background-color: $primaryDark;
  transition: background-color 0.5s ease;
}

.about__wrapper {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  max-width: 50vw;
}

.hi {
  animation: moveInL 1.5s cubic-bezier(1, 1.1, 0, 1) 0.5s 1;
  animation-fill-mode: forwards;
  visibility: hidden;
  font-size: 4rem;
}

@keyframes moveInL {
  0% {
    transform: translate(-1000%, 0);
    visibility: visible;
  }
  100% {
    transform: translate(0%, 0);
    visibility: visible;
  }
}

.intro__wrapper {
  margin: auto;
  max-width: 50rem;
}

.info {
  font-size: 1.5rem;
  text-align: left;
}

.skills-title {
  font-size: 2.5rem;
}

.skills__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.skills-title {
  margin: auto;
  max-width: 50rem;
  flex: 1;
}

.skill-list {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 1vw;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.skill-list img {
  filter: grayscale(10%);
  opacity: 50%;
  transition: all 0.2s ease-in-out;
  height: 150px;
  width: auto;
}

.skill-list img:hover {
  opacity: 100%;
  filter: grayscale(0%);
  transform: scale(1.1);
}

// Projects & Contact

.projects {
  background-color: $tertiaryLight;
}

#dark .projects {
  background-color: $secondaryDark;
  transition: background-color 0.5s ease;
}

.projects__wrapper {
  margin-left: auto;
  margin-right: auto;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 3vh;
}

#dark .projects__wrapper {
  color: $secondaryLight;
  transition: background-color 0.5s ease;
} 



.contact {
  background-color: $primaryLight;
  padding-bottom: 100px;
  margin-bottom: 100px;
  max-width: 100%;
  height: 100%;
}

#dark .contact {
  background-color: $primaryDark;
  transition: background-color 0.5s ease;
}

.contact__wrapper {
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactBtns {
  display: flex;
  gap: 2rem;
  justify-content: center;

  img {
    height: 60px;
    width: 60px;
  }
}

@media (max-width: 1300px) {
  .skill-list img {
    height: 100px;
    width: 100px;
  }

  .navbar {
    display: none;
  }

  .hb-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 900px) {
  .hero {
    min-height: 70vh;
  }

  .dev-title,
  .web-title,
  .first-name,
  .last-name {
    display: block;
    font-size: 13vw;
  }

  .dev-title {
    color: white;
  }

  .web-title {
    color: $tertiaryLight;
  }

  #dark .web-title {
    color: $secondaryLight;
    transition: background-color 0.5s ease;
  }

  .about__wrapper {
    margin: 0;
    position: relative;
  }

  .intro__wrapper {
    padding: 0;
    margin: 0;
  }

  .info {
    text-align: center;
    font-size: 1.2rem;
    margin-bottom: 20px;
    animation: moveInL 3s cubic-bezier(1, 1.1, 0, 1) 0.5s 1;
    animation-fill-mode: forwards;
    visibility: hidden;
    margin-left: -60px;
    margin-right: -60px;
  }

  .skill-list img {
    filter: grayscale(0%);
    opacity: 100%;
    height: 70px;
    width: auto;
  }

  .skill-list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 5vw;
    justify-content: center;
    align-items: center;
    margin-left: -40px;
    margin-right: -40px;
  }
}
